<template>
  <div class="pwd-login-container">
    <div class="login-tab clearfix">
      <router-link tag="div" to="/37duc/user/smsLogin" class="login-tab-item">
        <span class="login-link">短信登录</span>
      </router-link>
      <router-link tag="div" to="/37duc/user/pwdLogin" class="login-tab-item">
        <span class="login-link">密码登录</span>
      </router-link>
    </div>
    <form action="" @submit.prevent="submit" class="comForm">
      <div class="input-wrapper">
        <input type="text" v-model.trim="sendForm.idNum" :class="errorIdNum?'active':''" @focus="focus($event,'idNum')"
               @blur="blur($event,'idNum')" placeholder="身份证号">
        <i class="del" v-show="sendForm.idNum" @mousedown.stop="del('idNum')" :style="delImg"></i>
        <span class="error">{{errorIdNum}}</span>
      </div>
      <div class="input-wrapper clearfix">
        <input type="password" v-model.trim="sendForm.pwd" autocomplete="off" @focus="focus($event)"
               @blur="blur($event,'pwd')" placeholder="密码">
        <i class="del" v-show="sendForm.pwd" @mousedown.stop="del('pwd')" :style="delImg"></i>
      </div>
      <div class="submit-wrapper">
        <div class="error-tips" v-show="errorTips">{{errorTips}}</div>
        <input type="submit" v-model="loginBtnText" class="submit" :class="allValid? 'active':''"
               :disabled="!allValid||isRequesting">
      </div>
      <div class="clearfix forget-register-wrapper">
        <router-link tag="div" to="/37duc/user/forgetPassword" class="forget-btn">
          <span class="forget-link">忘记密码</span>
        </router-link>
        <router-link tag="div" to="/37duc/user/register" class="register-btn">
          <span class="register-link">立即注册</span>
        </router-link>
      </div>
    </form>
  </div>
</template>

<script>
import { getComFormMixin } from '@/assets/js/mixins/comForm'
import { ValidateUtil } from '@/assets/js/global'
// import { uuid } from '@/assets/js/util'
import { mapMutations } from 'vuex'

export default {
  name: 'pwdLogin',
  mixins: [getComFormMixin],
  data () {
    return {
      loginBtnText: '登录',
      isRedicturl: false,
      isRedicturlProductId: '',
      // isMessageCode: false,
      sendForm: {
        idNum: '',
        pwd: ''
      }
    }
  },
  mounted () {
  },
  methods: {
    submit () {
      this.loginBtnText = '登录中....'
      this.isRequesting = true
      let postData = {
        idNum: this.sendForm.idNum,
        password: this.sendForm.pwd
      }
      this.$api.user.pwdLogin(postData).then((res) => {
        this.isRequesting = false
        this.loginBtnText = '登录'
        if (res.code === 1) {
          this.SET_TOKEN(res.data.csrfToken)
          this.SET_USER_INFO(res.data.member)
          if (this.isRedicturl) {
            this.$router.push({
              path: `/37duc/product/detail/${this.isRedicturlProductId}`,
              query: {
                productId: this.isRedicturlProductId
              }
            })
          } else {
            this.$router.push('/37duc/personCenter')
          }
        } else {
          this.errorTips = res.message
        }
      })
    },
    blur (event, name) {
      this._onBlur(event)
      switch (name) {
        case 'idNum':
          this._validIdNum()
          break
      }
    },
    _validIdNum () {
      if (this.sendForm.idNum) {
        if (!ValidateUtil.idCardRegexs(this.sendForm.idNum).pass) {
          this.errorIdNum = ValidateUtil.idCardRegexs(this.sendForm.idNum).tip
        }
      }
    },
    ...mapMutations([
      'SET_TOKEN',
      'SET_USER_INFO'

    ])
  },
  computed: {
    allValid () {
      return this.sendForm.idNum && this.sendForm.pwd && !this.errorIdNum
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => { //  这里的vm指的就是vue实例，可以用来当做this使用
      if (from.params.redirctUrl) {
        vm.isRedicturl = true
        vm.isRedicturlProductId = from.params.productId
      }
    })
  },
  components: {}

}
</script>
<style scoped lang="scss">
  @import "@/assets/styles/comForm.scss";

  .comForm {
    padding-top: 20px;
  }
</style>
